import React from 'react';
import { motion, AnimatePresence } from 'framer-motion';
import './DataPanel.css';

const DataPanel = ({ summaryData, blockData, whaleData }) => {
  const formatCurrency = (amount) => {
    return new Intl.NumberFormat('en-US', {
      style: 'currency',
      currency: 'USD',
      minimumFractionDigits: 0,
      maximumFractionDigits: 0
    }).format(amount);
  };

  const formatTime = (timestamp) => {
    return new Date(timestamp * 1000).toLocaleTimeString();
  };

  return (
    <div className="data-panel">
      {/* Summary Statistics */}
      <motion.div 
        className="data-card summary-card"
        initial={{ opacity: 0, y: 20 }}
        animate={{ opacity: 1, y: 0 }}
        transition={{ duration: 0.5 }}
      >
        <h3>üìä Summary Statistics</h3>
        {summaryData ? (
          <div className="summary-grid">
            <div className="stat-item">
              <span className="stat-label">Transactions</span>
              <span className="stat-value">{summaryData.count}</span>
            </div>
            <div className="stat-item">
              <span className="stat-label">Total Volume</span>
              <span className="stat-value">{summaryData.total_btc.toFixed(2)} BTC</span>
            </div>
            <div className="stat-item">
              <span className="stat-label">USD Value</span>
              <span className="stat-value">{formatCurrency(summaryData.total_usd)}</span>
            </div>
            <div className="stat-item">
              <span className="stat-label">Whales Detected</span>
              <span className="stat-value highlight">{summaryData.whales}</span>
            </div>
            <div className="stat-item">
              <span className="stat-label">Average Size</span>
              <span className="stat-value">{summaryData.avg_btc.toFixed(2)} BTC</span>
            </div>
            <div className="stat-item">
              <span className="stat-label">Last Updated</span>
              <span className="stat-value">{formatTime(summaryData.timestamp)}</span>
            </div>
          </div>
        ) : (
          <div className="loading-state">
            <div className="pulse-dot"></div>
            <span>Waiting for data...</span>
          </div>
        )}
      </motion.div>

      {/* Recent Whale Transactions */}
      <motion.div 
        className="data-card whales-card"
        initial={{ opacity: 0, y: 20 }}
        animate={{ opacity: 1, y: 0 }}
        transition={{ duration: 0.5, delay: 0.1 }}
      >
        <h3>üêã Recent Whales</h3>
        <div className="whales-list">
          <AnimatePresence>
            {whaleData.slice(0, 5).map((whale, index) => (
              <motion.div
                key={`${whale.hash}-${whale.timestamp}`}
                className="whale-item"
                initial={{ opacity: 0, x: -20 }}
                animate={{ opacity: 1, x: 0 }}
                exit={{ opacity: 0, x: 20 }}
                transition={{ duration: 0.3, delay: index * 0.1 }}
              >
                <div className="whale-header">
                  <span className="whale-time">{formatTime(whale.timestamp)}</span>
                  <span className="whale-value">{whale.value_btc.toFixed(2)} BTC</span>
                </div>
                <div className="whale-details">
                  <span className="whale-usd">{formatCurrency(whale.value_usd)}</span>
                  <a 
                    href={`https://www.blockchain.com/btc/tx/${whale.hash}`}
                    target="_blank"
                    rel="noopener noreferrer"
                    className="whale-hash"
                  >
                    {whale.hash.slice(0, 8)}...
                  </a>
                </div>
              </motion.div>
            ))}
          </AnimatePresence>
          {whaleData.length === 0 && (
            <div className="empty-state">
              <span>No whale transactions yet</span>
            </div>
          )}
        </div>
      </motion.div>

      {/* Recent Blocks */}
      <motion.div 
        className="data-card blocks-card"
        initial={{ opacity: 0, y: 20 }}
        animate={{ opacity: 1, y: 0 }}
        transition={{ duration: 0.5, delay: 0.2 }}
      >
        <h3>üîó Recent Blocks</h3>
        <div className="blocks-list">
          <AnimatePresence>
            {blockData.map((block, index) => (
              <motion.div
                key={`${block.height}-${block.timestamp}`}
                className="block-item"
                initial={{ opacity: 0, x: -20 }}
                animate={{ opacity: 1, x: 0 }}
                exit={{ opacity: 0, x: 20 }}
                transition={{ duration: 0.3, delay: index * 0.05 }}
              >
                <div className="block-info">
                  <span className="block-time">{formatTime(block.timestamp)}</span>
                  {block.height && (
                    <span className="block-height">Height: {block.height}</span>
                  )}
                  {block.n_tx && (
                    <span className="block-txs">{block.n_tx} transactions</span>
                  )}
                </div>
              </motion.div>
            ))}
          </AnimatePresence>
          {blockData.length === 0 && (
            <div className="empty-state">
              <span>No blocks detected yet</span>
            </div>
          )}
        </div>
      </motion.div>
    </div>
  );
};

export default DataPanel;
